@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
.gridProdutos {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  grid-row-gap: 50px;
  row-gap: 50px;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;

  margin-top: 2rem;
}

.gridItem {
  overflow: hidden;
}

.gridItem img {
  border-radius: 15px;

  width: 95%;
  height: auto;
  object-fit: cover;
}

.enterAnimation {
  -webkit-animation:showLeft .4s forwards;
          animation:showLeft .4s forwards;
}

@-webkit-keyframes showLeft {
  from {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  } to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes showLeft {
  from {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  } to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
.gridContato {
  display: grid;
  grid-template-columns: 45% 55%;
  margin-top: 2rem;
}

.imgContato {
  grid-column: 1;
}

.imgContato img {
  width: 90%;
  height: auto;
  
  border-radius: 20px;
  object-fit: contain;
}

.conteudoContato {
  grid-column: 2;
  padding: 15px;
}

.conteudoContato div {
  display: flex;
  flex-direction: column;
  grid-gap: 6px;
  gap: 6px;

  font-size: 1.3rem;
  font-family: 'Roboto', sans-serif;
}

.conteudoContato div h2 {
  font-size: 1.7rem;
  margin: 0 0 .8rem 0;
}

.conteudoContato div p::before {
  content: '';
  display: inline-block;
  width: 15px;
  height: 3px;
  background: rgba(0, 0, 0, 0.685);
}

.conteudoContato div a {
  display: flex;
  justify-content: flex-start;
  color: rgba(0, 0, 0, 0.699);
  grid-gap: 1rem;
  gap: 1rem;
}

.conteudoContato div img {
  width: 32px;
  height: 32px;
}

